.loadContainer {
}

.contractDeleteButton {
  float: right;
  position: relative;
  top: -12px;
  opacity: 0.8;
}

.contractDeleteButton:hover {
    opacity: 1;
  }

.editor {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding-left: 1em;
}

.editor p {
    line-height: 48px;
    height: 48px;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    margin: 0;
    font-size: 1.2em;
  }

.confirmRemoval {
  text-align: center;
}

.confirmRemoval .editor {
    text-align: left;
    margin-top: 0.5em;
  }



