.list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.list .item {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex: 0 1 50%;
        flex: 0 1 50%;
    padding-bottom: 0.25em;
    position: relative;
    width: 50%;
  }

.list .item:nth-child(odd) {
      padding-right: 0.125em;
    }

.list .item:nth-child(even) {
      padding-left: 0.125em;
    }

.empty {
  display: block;
  width: 100%;
}

.empty div {
    color: #aaa;
  }



